$primaryColor: #4527a4;

.wp-block-b-blocks-profile-card {

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body {
    // min-height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    // background: linear-gradient(135deg, #0f172a, #1e293b);
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    color: white;
    // padding: 2rem;
    // margin-top: 20px;

}

.profile-grid{
    display: grid;
    grid-template-columns: repeat(2, 1fr); 
    gap: 10px;
    width: 50rem;
    margin: 0 auto;
}


/* Tablet View  */
@media (max-width: 1024px) {
    .profile-grid {
        grid-template-columns: repeat(2, 1fr); 
        width: 44rem;
    }
}

/* Mobile View  */
@media (max-width: 768px) {
    .profile-grid {
        grid-template-columns: repeat(2, 1fr); 
    }
}

/* Small Mobile View */
@media (max-width: 480px) {
    .profile-grid {
        grid-template-columns: 1fr;
        width: 19rem; 
    }
}

.container {
    width: 100%;
    max-width: 500px;
}

/* Profile Card */
.profile-card {
    position: relative;
    padding: 2.5rem;
    background: rgba(30, 41, 59, 0.8);
    backdrop-filter: blur(10px);
    border-radius: 1.5rem;
    overflow: hidden;
    transition: transform 0.3s ease;
}

.profile-card:hover {
    transform: translateY(-5px);
}

.card-content {
    position: relative;
    z-index: 2;
}

/* Avatar Styles */
.avatar-wrapper {
    position: relative;
    width: 120px;
    height: 120px;
    margin: 0 auto 2rem;
    
.avatar {
    // position: relative;
    width: 100%;
    height: 100%;
    border-radius: 50%;
    overflow: hidden;
    .avatarImg{
        width: 100%;
        height: 100%;
    }
}

.avatar:hover .avatar-glow {
    opacity: 0.5;
}

}

/* Profile Info */
.profile-info {
    text-align: center;
    .name {
        margin-bottom: 0.5rem;
        background: linear-gradient(to right, #60a5fa, #a78bfa);
        background-clip: text;
        -webkit-text-fill-color: transparent;
    }
    
    .title {
        color: #94a3b8;
        // font-size: 1rem;
        margin-bottom: 2rem;
    }
    
    /* Stats Section */
    .stats {
        display: flex;
        justify-content: space-around;
        margin-bottom: 2rem;
        padding: 1rem 0;
        border-top: 1px solid rgba(255, 255, 255, 0.1);
        border-bottom: 1px solid rgba(255, 255, 255, 0.1);
        .stat {
            display: flex;
            flex-direction: column;
            gap: 0.5rem;

            .stat-value {
                font-size: 1.2rem;
                font-weight: bold;
                // background: linear-gradient(to right, #60a5fa, #a78bfa);
                color: #a78bfa;
                // background-clip: text;
                // -webkit-text-fill-color: transparent;
            }
            
            .stat-label {
                font-size: 0.9rem;
                color: #2D3033;
            }
        }
        
    }

    /* Bio Section */
    .bio {
    color: #94a3b8;
    line-height: 1.6;
    margin-bottom: 2rem;
    padding: 0 1rem;
    }
   /* Skills Section */
    .skills {
    display: flex;
    flex-wrap: wrap;
    gap: 0.8rem;
    justify-content: center;
    margin-bottom: 2rem;
    .skill {
        padding: 0.5rem 1rem;
        // background: rgba(255, 255, 255, 0.05);
        border-radius: 2rem;
        font-size: 0.9rem;
        // color: #94a3b8;
        border: 1px solid rgba(255, 255, 255, 0.1);
        transition: all 0.3s ease;
    }
    
    // .skill:hover {
    //     background: rgba(255, 255, 255, 0.1);
    //     color: white;
    //     transform: translateY(-2px);
    // }
    }
    
   /* Action Buttons */
   .actions {
    display: flex;
    flex-wrap: wrap; 
    gap: 1rem; 
    justify-content: center; 
    align-items: center;
    margin-top: 20px;

    .action-btn {
        position: relative;
        padding:16px 40px ;
        border: none;
        border-radius: 0.8rem;
        font-size: 1rem;
        cursor: pointer;
        overflow: hidden;
        transition: all 0.3s ease;
        .action-btn span {
            position: relative;
            z-index: 1;
        }
        .btn-effect {
            position: absolute;
            inset: 0;
            transition: opacity 0.3s ease;
        }
    }
    .primary {
        background: linear-gradient(45deg, #60a5fa20, #a78bfa20);
        color: white;
        border: 1px solid rgba(255, 255, 255, 0.1);
    }
    
    .primary .btn-effect {
        background: linear-gradient(45deg, #60a5fa, #a78bfa);
        opacity: 0;
    }
    
    .primary:hover .btn-effect {
        opacity: 0.2;
    }
    .secondary {
        background: transparent;
        color: #94a3b8;
        border: 1px solid rgba(255, 255, 255, 0.1);
    }
    
    .secondary .btn-effect {
        background: rgba(255, 255, 255, 0.1);
        opacity: 0;
    }
    
    .secondary:hover .btn-effect {
        opacity: 1;
    }
    
   }
}


/* Card Effects */
.card-shine {
    position: absolute;
    top: 0;
    left: -100%;
    width: 100%;
    height: 100%;
    background: linear-gradient(
        90deg,
        transparent,
        rgba(255, 255, 255, 0.1),
        transparent
    );
    animation: shine 8s infinite;
}

@keyframes shine {
    to {
        left: 200%;
    }
}

.card-border {
    position: absolute;
    inset: 0;
    border: 2px solid transparent;
    border-radius: 1.5rem;
    background: linear-gradient(45deg, #60a5fa, #a78bfa) border-box;
    mask: 
        linear-gradient(#fff 0 0) padding-box,
        linear-gradient(#fff 0 0);
    -webkit-mask-composite: destination-out;
    mask-composite: exclude;
    opacity: 0;
    transition: opacity 0.3s ease;
}

.profile-card:hover .card-border {
    opacity: 1;
}

.card-glow {
    position: absolute;
    inset: 0;
    background: linear-gradient(45deg, #60a5fa, #a78bfa);
    border-radius: 1.5rem;
    opacity: 0;
    filter: blur(20px);
    transition: opacity 0.3s ease;
}

.profile-card:hover .card-glow {
    opacity: 0.05;
}

/* Responsive Design */
@media (max-width: 480px) {
    .container {
        padding: 1rem;
    }

    .profile-card {
        padding: 1.5rem;
    }

    .avatar-wrapper {
        width: 100px;
        height: 100px;
    }

    .name {
        font-size: 1.5rem;
    }

    .skills {
        flex-wrap: wrap;
    }
}


}

